import React, { forwardRef } from "react";
import ReactModal from "react-modal";

import { getCustomDrawerStyles } from "./Drawer.styles";
import { DrawerProps } from "./Drawer.types";
import { DrawerContext } from "./DrawerContext";

const Drawer = forwardRef<ReactModal, DrawerProps>(({
	dataQA,
	children,
	onRequestClose,
	appElement,
	preventBackgroundScroll = true,
	isOpen,
	className,
	hideCloseButton = false,
	$zIndex,
	size,
	...props
}, ref) => {
	const disableScroll = () => {
		if (preventBackgroundScroll) document.body.style.overflow = "hidden";
	};
	const enableScroll = () => {
		if (preventBackgroundScroll) document.body.style.overflow = "unset";
	};
	return (
		<DrawerContext.Provider value={{ onRequestClose, hideCloseButton }}>
			<ReactModal
				appElement={appElement}
				style={getCustomDrawerStyles({ $zIndex, size })}
				onRequestClose={onRequestClose}
				data={{
					qa: dataQA,
				}}
				onAfterOpen={disableScroll}
				onAfterClose={enableScroll}
				ref={ref}
				isOpen={isOpen}
				className={className}
				{...props}
			>
				{children}
			</ReactModal>
		</DrawerContext.Provider>
	);
});

export default Drawer;
